<template>
  <div>
    <vue-confirm-dialog></vue-confirm-dialog>

    <div class="row">
      <div class="col-md-12" ref="table">
        <v-card>
          <v-card-title>
            Records

            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>

          <v-data-table
            :headers="headers"
            :items="records"
            :search="search"
            item-key="id"
          >
            <template v-slot:item.guest="{ item }">
              <td class="text-right">
                <span
                  v-if="item.guest == 0"
                  class="label label-lg label-light-danger label-inline"
                >
                User
                  
                </span>
                <span
                  v-if="item.guest == 1"
                  class="label label-lg label-light-success label-inline"
                >
                   Guest
                </span>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </div>
    </div>

    <hr id="formData" />
  </div>
</template>

<script>
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

export default {
  data() {
    return {
      records: [],
      search: "",
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },
        { text: "User", value: "name" },
        { text: "Phone", value: "phone" },
        { text: "Guest", value: "guest" },

        { text: "Created at", value: "created_at" },
        //{ text: "Actions", value: "actions", sortable: false },
      ],
    };
  },
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: "  Guest Users", route: "people" },
    ]);
  },
  created() {
    this.fetchRecords();
  },
  methods: {
    fetchRecords() {
      this.$ApiService
        .get("/People")
        .then((response) => {
          //console.log(response.data);
          this.records = response.data.data;
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
  }, // end of methods
}; //end of class
</script>
